<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import * as partnerData from '../data/partners';

const confetti = require('canvas-confetti');

var lodash = require('lodash');

export default {
  name: 'home',
  components: {
    // HelloWorld
  },
    data() {
        return {
            partnerList: [],
            display: false,
            currentList: [],
            opacityColor: 0,
            winnersLastName: 'ding',
            winnersFirstName: 'just',
            menuShow: false,
            winnersList: [],
            winnersRecord: false,
            lotterySet: false,
            luckyGuy: 1,
            recordList: [],
            secondValue: 10,
            manualMode: false,
            interval: null,
            prizeType: '有钱任性想抽奖',
            countFlag: 0,
            getReady: true,
            winnersShow: false,
        };
    },
    mounted: function () {
        this.initdata();
    },
    methods: {
        initdata(){
            let dataSource = JSON.parse(sessionStorage.getItem('dataList') ? sessionStorage.getItem('dataList') : JSON.stringify(partnerData.partners));
            this.winnersList.length = this.partnerList.length = this.recordList.length = 0;
            let dataArry = dataSource.data ? dataSource.data : dataSource;
            dataArry.forEach((item)=>{
                if(item.is_winner){
                    this.winnersList.push(item);
                }else {
                    this.partnerList.push(item);
                }
            });
            if(dataSource.record) this.recordList = dataSource.record;
        },
        lotterySetting(){
            this.luckyGuy = Math.round(this.luckyGuy);
            this.secondValue = Math.round(this.secondValue);
            console.log(this.luckyGuy);
            console.log(this.secondValue);
            let len = this.partnerList.length;
            if(len == 0){
                alert('没有可以参与抽奖的小伙伴了！');
            }else {
                if(this.luckyGuy < 1){
                    alert('至少需要一个人成为幸运儿！');
                    this.luckyGuy = 1;
                    return;
                }else if(this.luckyGuy > len){
                    alert('将来我们会发展成这么多人的大公司，但现在，数数在场的小伙伴吧！');
                    this.luckyGuy = 1;
                    return;
                }
                if(this.secondValue < 1){
                    alert('至少需要1秒吧！');
                    this.secondValue = 6;
                    return;
                }
            }
            this.lotterySet = false;
        },
        lotteryMenu(){
            if(this.display){
                alert('设置参数很重要，请先准备好！');
            }else {
                this.lotterySet = !this.lotterySet;
            }
        },
        confetti(){
            var end = Date.now() + (12 * 1000);
            var colors = ['#bb0000', '#ffffff'];
            var interval = setInterval(function() {
                if (Date.now() > end) {
                    return clearInterval(interval);
                }
                confetti({
                    startVelocity: 30,
                    spread: 360,
                    ticks: 60,
                    origin: {
                        x: Math.random(),
                        y: Math.random() - 0.2
                    }
                });
            }, 100);
            (function frame() {
                confetti({
                    particleCount: 2,
                    angle: 60,
                    spread: 55,
                    origin: {
                        x: 0
                    },
                    colors: colors
                });
                confetti({
                    particleCount: 2,
                    angle: 120,
                    spread: 55,
                    origin: {
                        x: 1
                    },
                    colors: colors
                });
                if (Date.now() < end) {
                    requestAnimationFrame(frame);
                }
            }());
        },
        clearWinnersList(){
            if(!confirm("确定重置数据吗？")) return;
            if(this.display){
                alert('重置数据也要先做好准备！');
            }else {
                if (sessionStorage.getItem("dataList")) {
                    sessionStorage.clear();
                }
                this.initdata();
            }
        },
        dataDefinition(){
            alert('还没做啦！老是点我干嘛');
        },
        openLucky(){
            this.countFlag++;
            if((this.countFlag == 1)&&(this.interval)&&this.display){
                LuckyCard.case({
                    coverColor: '#CCCCCC',
                    ratio: 0.5,
                    callback: () => {
                        this.getReady = true;
                    }
                });
                this.opacityColor = 0.9;
                this.confetti();
                return clearInterval(this.interval);
            }else {
                alert('还没开始就已结束？！');
            }
        },
        // confirmLottery(){
        //     let r = confirm("确定开始抽奖吗？");
        //     if (r == true) this.toggleContent();
        // },
        toggleContent(){
            if(this.partnerList.length == 0){alert('没有可以参与抽奖的小伙伴了！'); return;}
            if(!this.getReady){alert('请等待刮奖完成噢！'); return;}
            if(!this.display&&(!confirm("确定开始抽奖吗？"))) return;
            this.display = !this.display;
            this.opacityColor = 0;
            this.winnersShow = false;
            if(this.display){
                this.getReady = false;
                this.countFlag = 0;
                this.partnerList = lodash.shuffle(this.partnerList);
                var end = Date.now() + (this.secondValue * 1000);
                this.winnersShow = true;
                if(!this.manualMode){
                    this.interval = setInterval(() => {
                        if (Date.now() > end) {
                            clearInterval(this.interval);
                            LuckyCard.case({
                                coverColor: '#CCCCCC',
                                ratio: 0.5,
                                callback: () => {
                                    this.getReady = true;
                                }
                            });
                            this.opacityColor = 0.9;
                            this.confetti();
                            return ;
                        }
                        let lucky = lodash.sampleSize(this.partnerList, this.luckyGuy);
                        console.log(`幸运儿：`+JSON.stringify(lucky));
                        this.currentList = lucky;
                    }, 100);
                }else {
                    this.interval = setInterval(() => {
                        let lucky = lodash.sampleSize(this.partnerList, this.luckyGuy);
                        console.log(`幸运儿：`+JSON.stringify(lucky));
                        this.currentList = lucky;
                    }, 100);
                }
            }else {
                this.recordList.push(this.currentList);
                this.currentList.forEach((item) => {
                    item.is_winner = true;
                    item.prize_level = this.prizeType;
                    this.winnersList.push(item);
                    let oldindex = lodash.findIndex(this.partnerList, item);
                    this.partnerList.splice(oldindex, 1);
                });
                sessionStorage.setItem('dataList', JSON.stringify({
                    data: (this.winnersList).concat(this.partnerList),
                    record: this.recordList
                }));
                let hhh = sessionStorage.getItem('dataList');
                // console.log(`备份数据：` + hhh);
                this.currentList = [];
                let cardlist = document.querySelectorAll('.cover');
                cardlist.forEach((item) =>{
                    item.remove();
                })
            }
        }
    },
}
</script>
