import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'normalize.css'
import 'nes.css/css/nes.min.css'
import './data/emoji.min.css'

import './data/scratchcard.css'
import scratchcard from './data/scratchcard'
Vue.use(scratchcard)

Vue.config.productionTip = true

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
